body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Times New Roman', Times, serif;
  box-sizing: border-box;
  margin: 0;
}

/* Defining colours to standardise, reduce typos and allow easier specification */
:root {
--blue-color: #2B4451;
--brown-color: #BA8B69;
--grey-color: #B7C7D4;
}

/* Used on the Welcome section */
h1 {
  color: white;
  font-size: 55px;
  margin: 0px;
}

/* Used for section headings */
h2 {
  text-align: center;
  padding-top: 70px;
  color:  #2B4451;
  color:  var(--blue-color);
  font-size: 18px;
}

/* Used on the welcome section */
h3 {
  color:  #2B4451;
  color:  var(--blue-color);
  margin-bottom: 5%;
  margin-top: 5%;
  font-size: 18px;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Navigation {
  background: var(--blue-color);
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  height: 70px;
  padding: 0 50px;
  position: fixed;
  width: 100%;
  z-index: 100;
}

.logo {
  color: var(--grey-color);
  font-size: 28px;
}

ul.NavigationMenus {
  display: flex;
  flex: 1 1;
  padding: 0 0 0 40px;
}

ul.NavigationMenus li {
  list-style: none;
  padding: 0 15px;
}

ul.NavigationMenus li a {
  color: var(--grey-color);
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
}

ul.NavigationMenus li a:hover {
  color: var(--brown-color);
}

.SearchIcon img {
  width: 22px;
  background-color: var(--blue-color);
}

.SearchBox {
  display: flex;
  height: 40px;
  padding: 2px;
  background: var(--grey-color);
  min-width: 18%;
  position: absolute;
  top: 80px;
  right: 50px;
  opacity: 0;
  pointer-events: none;
  transition: top 0.3s ease, opacity 0.1s ease;
  top: 95px;
  opacity: 1;
  pointer-events: auto;
}

.search-icon {
  display: block;
}

.search-icon.hide {
  display: none;
}


.SearchData {
  width: 100%;
  height: 100%;
  padding: 0 10px;
  color: var(--brown-color);
  font-size: 8px;
  border: none;
  font-weight: 500;
  background: none;
}

.SearchButton {
  padding: 0 15px;
  color: var(--grey-color);
  font-size: 8px;
  background: var(--brown-color);
  border: none;
  cursor: pointer;
}

.SearchButton:hover {
  background: #e63600;
}

.CancelIcon,
.SearchIcon {
  width: 40px;
  text-align: center;
  margin: 0 0px;
  font-size: 18px;
  color: var(--grey-color);
  cursor: pointer;
}

.menu-icon span,
.cancel-icon {
  display: none;
}

.cancel-icon.show {
  display: block;
}
.OverlayContainer {
    width: auto;
    height: auto;
    opacity: 95;
    top: 0;
    left: 0;
    padding: 5%;
    margin-top: 5%;
    margin-left: 5%;
    position: fixed;
    background-color: var(--grey-color);
    overflow: auto;
    z-index: 110;
  }
  
  /* Darken behind the popup to give it more focus*/
  .OverlayBackground {
    z-index: 108;
    background-color: rgba(0, 0, 0, 0.75);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  /* This is where we will put a button to close the window */
  .OverlayControls {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .OverlayClose {
    border: none;
    background-color: transparent;
    font-size: 36px;
    cursor: pointer;
  }

  .OverlayClose:after {
    display: inline-block;
    content: "\D7";
  }


.CarouselContainer {
    /* max-width: 2000px;*/
     position: relative;
     margin: auto;
     z-index: 99;
 }
 
 .Carousel {
     background:rgba(0,0,0,0.6);
     /* display: none; */
 }
 
 /* Insert forward & back arrows */
 .Back, .Forward {
     cursor: pointer;
     position: absolute;
     top: 48%;
     width: auto;
     margin-top: -23px;
     padding: 17px;
     color: #DBECEA;
     font-weight: bold;
     font-size: 19px;
     transition: 0.4s ease;
     border-radius: 0 5px 5px 0;
     -webkit-user-select: none;
             user-select: none;
 }

 /* Place the "forward button" to the right */
.Forward {
    right: 0;
    border-radius: 4px 0 0 4px;
}

.Back {
    left: 0;
    border-radius: 4px 0 0 4px;
}

.Back:hover, .Forward:hover {
    background-color: rgba(0,0,0,0.8);
}

.Heading {
    color: var(--grey-color);
    font-size: 60px;
    padding: 10px 14px;
    position: absolute;
    bottom: 60%;
    right:10%;
}

.Info {
    color: var(--brown-color);
    font-size: 36px;
    padding: 10px 14px;
    position: absolute;
    right: 15%;
    bottom: 55%;
}

.Image {
    width: 100%;
}

.Welcome {
    display: grid;
    grid-template-columns: auto auto;
    padding-top: 8%;
    padding-bottom: 8%;
    padding-left: 15%;
}

.Welcome > div {
    grid-column-start: 1;
    grid-column-end: 2;
    position: relative;
}

.WelcomeOverlayMessage {
    grid-column-start: 1;
    grid-column-end: 2;
    position: absolute;
    background-color: var(--blue-color);
    top: 20%;
    left: 60%;
    padding-top: 4%;
    padding-left: 4%;
    padding-right: 4%;
    padding-bottom: 4%;
    box-shadow: rgba(0, 0, 0, 0.25) -2px 2px 25px 0px;
}

.H3Welcome {
    color: var(--brown-color);
    margin: 0px;
}

.PWelcome {
    color:white;
    margin-top: 4%;
}

.WelcomeOverlayMessage > button {
    background-color: var(--grey-color);
    text-align: center;
    font-size: 14px;
    font-weight: 700;
    padding-left: 10%;
    padding-right: 10%;
    border-style: none;
    color: white;
    padding-top: 3%;
    padding-bottom: 3%;
    margin-top: 4%;
}

.WelcomeOverlayMessage > button:hover {
  padding-right: 12%;
}


.Coaching {
    background-color: var(--brown-color);
}

.CoachingServices {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 5%;
    gap: 5%;
    background-color: var(--brown-color);
    padding: 10%;
    padding-top: 4%;
    width: 100%;
}

.CoachingServices > div {
    padding: none;
    margin: 0px;
    text-align: center;
    place-items: center;
    box-shadow: 0 5px 15px rgba(0,0,0,0.25);
    background-color: white;
    padding-bottom: 10%;
    width: 100%;
}

.CoachingServices > div:hover {
  /* Darken if the mouse goes over the box*/
  box-shadow: 0 5px 15px rgba(0,0,0,0.5);
}


.Button {
    background-color: var(--grey-color);
    font-size: 14px;
    font-weight: 700;
    padding-left: 18%;
    padding-right: 18%;
    border-style: none;
    color: var(--blue-color);
    padding-top: 3%;
