.PhotoGalleryGridContainer {
    display: grid;
    grid-template-columns: auto auto auto auto;
    padding-bottom: 5%;
    grid-gap: 0.5%;
    gap: 0.5%;
}

.PhotoGalleryGridContainer > figure {
    padding: 0px;
    margin: 0em;
}

.GalleryImage {
    width: 100%;
}

.Modal {
    position: fixed;
    z-index: 101; /*so it sits on top of everything (including the nav bar)*/
    padding-top: 10%;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.75);
  }
  
  /*Image in the foreground*/
  .ModalPhoto {
    margin: auto;
    display: block;
    width: 80%;
  }
  
  .ClosePhoto {
    position: absolute;
    top: 5%;
    right: 5%;
    color: white;   
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
  }

.ClosePhoto:hover,
.ClosePhoto:focus {
  color: var(--brown-color);
  text-decoration: none;
  cursor: pointer;
}
.Tips {
    background-color: var(--grey-color);
}

.TipsGridContainer {
    padding-top: 1%;
