}

.TipsGridVideo {
    grid-row-start: 1;
    grid-row-end: 6;
    background-color: var(--brown-color);
    box-shadow: -2px 2px 25px 0px rgba(0,0,0,0.25);
}

.TipsGridHeading {
    grid-column-start: 2;
