}

blockquote {
    font-style: italic;
    border: none;
    quotes: "\201C" "\201D" "\2018" "\2019";
    display: block;
    text-align: center;
}

blockquote:before {
    content: open-quote;
    font-weight: bold;
    color: var(--grey-color);
  }
  
  blockquote:after {
    content: close-quote;
    font-weight: bold;
    color: var(--grey-color);
  }

